import { useState, useEffect } from "react";
import { Link, useParams, useNavigate } from "react-router-dom";
import { useAuthContext } from "../../hooks/useAuthContext";
import Select from "react-select";
import config from "../../components/config";

const Updatestory = () => {
  const url = config.URL;
  const { user } = useAuthContext();
  const { id } = useParams();
  const paramId = id;
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [toggleForm, setToggleForm] = useState(false);
  const [felhasznalonev, setFelhasznalonev] = useState("");
  const [isAdmin, setIsAdmin] = useState("");

  const [cim, setCim] = useState("");
  const [szerzo, setSzerzo] = useState("");
  const [boritokep, setBoritokep] = useState("");
  const [leiras, setLeiras] = useState("");
  const [karakterek, setKarakterek] = useState("");
  const [nyelv, setNyelv] = useState("");
  const [kategoria, setKategoria] = useState("");
  const [story, setStory] = useState("");
  const [isPublished, setIsPublished] = useState(false);

  const [dropdownKategoria, setDropDownKategoria] = useState("");
  const [dropdownNyelv, setDropDownNyelv] = useState("");

  const navigate = useNavigate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(url + "/getInfos", {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${user.token}`,
          },
        });

        if (response.ok) {
          const data = await response.json();
          const isAdmin = data.isAdmin;
          const felhasznalonev = data.felhasznalonev;
          setFelhasznalonev(felhasznalonev);
          setIsAdmin(isAdmin);
        }
      } catch (error) {
        console.log("Fetch error:", error);
      }
    };

    fetchData();
  }, [user, felhasznalonev]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(url + "/onestory", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${user.token}`,
          },
          body: JSON.stringify({ id }),
        });

        if (response.ok) {
          const data = await response.json();
          const onestory = data.onestory[0];
          setCim(onestory.cim);
          setSzerzo(onestory.szerzo);
          setBoritokep(onestory.boritokep);
          setKarakterek(onestory.karakterek);
          setLeiras(onestory.leiras);
          setStory(onestory.story);
          setIsPublished(onestory.isPublished);

          if (dropdownKategoria.length > 0 && dropdownNyelv.length > 0) {
            let loadKategoria = {
              value: onestory.kategoria,
              label: onestory.kategoria,
            };

            let loadNyelv = {
              value: onestory.nyelv,
              label: onestory.nyelv,
            };

            setKategoria(loadKategoria);
            setNyelv(loadNyelv);
          }

          setSuccess(data.msg);
        } else {
          const data = await response.json();
          setError(data.msg);
        }
      } catch (error) {
        console.log("Fetch error:", error);
      }
    };

    fetchData();
  }, [user, id, dropdownKategoria, dropdownNyelv]);

  useEffect(() => {
    setIsLoading(true);
    const fetchDropdownCategory = async () => {
      try {
        const adat = await fetch(url + "/kategoria", {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${user.token}`,
          },
        });

        if (adat.ok) {
          const response = await adat.json();
          const CategoryOptions = response.kategoria.map((option) => ({
            label: option.kategoria,
            value: option.kategoria,
          }));
          setDropDownKategoria(CategoryOptions);
          setIsLoading(false);
        } else {
          const response = await adat.json();
          setError(response.msg);
        }
      } catch (error) {
        console.log(error);
      }
    };

    fetchDropdownCategory();
  }, [user]);

  useEffect(() => {
    const fetchDropdownLanguage = async () => {
      try {
        const adat = await fetch(url + "/nyelv", {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${user.token}`,
          },
        });

        if (adat.ok) {
          const response = await adat.json();
          const LanguageOptions = response.nyelv.map((option) => ({
            label: option.nyelv,
            value: option.nyelv,
          }));
          setDropDownNyelv(LanguageOptions);
        } else {
          const response = await adat.json();
          setError(response.msg);
        }
      } catch (error) {
        console.log(error);
      }
    };

    fetchDropdownLanguage();
  }, [user]);

  const handleDropdownCategory = (selectedOption) => {
    setKategoria({ value: selectedOption.value, label: selectedOption.label });
  };

  const handleDropdownLanguage = (selectedOption) => {
    setNyelv({ value: selectedOption.value, label: selectedOption.label });
  };

  const selectStyles = {
    control: (provided, state) => ({
      ...provided,
      lineHeight: "28px",
      border: state.isFocused ? "3px solid #f99417" : "3px solid transparent",
      borderBottomColor: "#f99417",
      borderRadius: state.isFocused ? "1rem" : "0",
      outline: state.isFocused && "none",
      width: "560px",
      backgroundColor: "transparent",
      borderColor: state.isFocused ? "#f99417" : "transparent",
      color: "#363062",
      transition:
        "border-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), padding 0.3s cubic-bezier(0.645, 0.045, 0.355, 1)",
      transform: "scale(1)",
      "&:hover": {
        borderColor: "#f99417",
        borderRadius: "1rem",
      },
      fontFamily: '"Martian Mono", monospace',
      fontSize: "9px",
    }),
    singleValue: (provided) => ({
      ...provided,
      color: "#363062",
      fontFamily: '"Martian Mono", monospace',
      fontSize: "11px",
    }),
    menu: (provided) => ({
      ...provided,
      width: "560px",
      marginTop: "0",
      borderRadius: "1rem",
    }),
    option: (provided, state) => ({
      ...provided,
      padding: "1rem",
      borderRadius: "1rem",
      backgroundColor: state.isFocused ? "#f99417" : null,
      color: state.isFocused ? "#fff" : "#f99417",
    }),
    placeholder: (provided, state) => ({
      ...provided,
      opacity: state.isFocused ? 0 : 1,
      transition: "opacity 0.3s cubic-bezier(0.645, 0.045, 0.355, 1)",
      color: "#2c3639",
      fontFamily: '"Martian Mono", monospace',
      fontSize: "11px",
    }),
  };

  const tovabb = () => {
    setToggleForm(!toggleForm);
    console.log(toggleForm);
  };

  const modosit = (event) => {
    event.preventDefault();

    if (!user) {
      setError("Nem vagy bejelentkzve!");
      return;
    }

    const adatok = {
      paramId,
      cim,
      szerzo,
      boritokep,
      leiras,
      karakterek,
      nyelv: nyelv.value,
      kategoria: kategoria.value,
      story,
      isPublished,
    };
    if (toggleForm == true) {
      const elkuld = async () => {
        setIsLoading(true);
        setError(null);
        setSuccess(null);

        if (
          cim.trim() === "" ||
          szerzo.trim() === "" ||
          boritokep.trim() === "" ||
          leiras.trim() === "" ||
          karakterek.trim() === "" ||
          story.trim() === ""
        ) {
          setError("Nem maradhat res cella!");
          setIsLoading(false);
          return;
        }

        const adat = await fetch(url + "/updatestory", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${user.token}`,
          },
          body: JSON.stringify(adatok),
        });

        if (adat.ok) {
          const response = await adat.json();
          setIsLoading(false);
          setSuccess(response.msg);
          navigate(`/profil/${felhasznalonev}`);
        } else {
          const response = await adat.json();
          setIsLoading(false);
          setError(response.msg);
        }
      };

      elkuld();
    }
  };

  const togglePublikalas = () => {
    if (isPublished) {
      setIsPublished(false);
    } else {
      setIsPublished(true);
    }
  };

  function displayImage(e) {
    const fileInput = e.target;

    if (fileInput.files && fileInput.files[0]) {
      const reader = new FileReader();

      reader.onload = function (e) {
        setBoritokep(e.target.result);
      };

      reader.readAsDataURL(fileInput.files[0]);
    }
  }

  function resetPic() {
    setBoritokep("");
  }

  return (
    <div>
      {!isLoading ? <div></div> : <div className="loader"></div>}
      {felhasznalonev == szerzo || isAdmin ? (
        <div className="updateStory-container">
          {!toggleForm ? (
            <div className="form-container">
              <form
                onSubmit={modosit}
                className="storyform"
                id="file-storyform"
              >
                <div class="card">
                  <div class="card__content">
                    <input
                      value={""}
                      type="file"
                      accept="image/*"
                      className="file-input"
                      onChange={(e) => displayImage(e)}
                    />
                    {boritokep ? (
                      <img src={boritokep} alt="Uploaded Image" />
                    ) : (
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="1em"
                        height="1em"
                        stroke-linejoin="round"
                        stroke-linecap="round"
                        viewBox="0 0 24 24"
                        stroke-width="2"
                        fill="none"
                        stroke="currentColor"
                        className="icon"
                      >
                        <polyline points="16 16 12 12 8 16"></polyline>
                        <line y2="21" x2="12" y1="12" x1="12"></line>
                        <path d="M20.39 18.39A5 5 0 0 0 18 9h-1.26A8 8 0 1 0 3 16.3"></path>
                        <polyline points="16 16 12 12 8 16"></polyline>
                      </svg>
                    )}
                  </div>
                  <span>Ajnlott kpmret 300x400px</span>
                  <button id="reset" onClick={resetPic}>
                    Trl
                  </button>
                </div>
              </form>
              <form onSubmit={modosit} className="storyform">
                <div className="form-row">
                  <input
                    value={cim}
                    type="text"
                    placeholder="Trtnet cme"
                    className="input"
                    onChange={(e) => setCim(e.target.value)}
                  />
                </div>
                <div className="form-row">
                  <input
                    type="text"
                    placeholder="Szerz"
                    value={szerzo}
                    className="input"
                    readOnly
                    onChange={(e) => setSzerzo(e.target.value)}
                  />
                </div>
                <div className="form-row" id="select-row">
                  <Select
                    value={!toggleForm ? kategoria : kategoria.label}
                    className="custom-select"
                    placeholder="Kategria"
                    disabled={isLoading}
                    styles={selectStyles}
                    options={dropdownKategoria}
                    onChange={handleDropdownCategory}
                  />
                </div>
                <div className="form-row">
                  <Select
                    value={!toggleForm ? nyelv : nyelv.label}
                    className="custom-select"
                    placeholder="Nyelv"
                    disabled={isLoading}
                    styles={selectStyles}
                    options={dropdownNyelv}
                    onChange={handleDropdownLanguage}
                  />
                </div>
                <div className="form-row">
                  <input
                    value={karakterek}
                    type="text"
                    placeholder="Karakterek"
                    className="input"
                    onChange={(e) => setKarakterek(e.target.value)}
                  />
                </div>
                <div className="form-row">
                  <textarea
                    maxLength="200"
                    value={leiras}
                    type="text"
                    placeholder="rd le rviden mirl fog szlni a trtneted!"
                    className="input"
                    id="leirasText"
                    onChange={(e) => setLeiras(e.target.value)}
                  />
                </div>
                <p id="leirasCounter">{leiras.length}/200</p>
                <div className="button-row">
                  <button disabled={isLoading} onClick={tovabb}>
                    Tovbb
                  </button>
                </div>
              </form>
            </div>
          ) : (
            <div className="addstory-container">
              <div className="textarea">
                <textarea
                  type="text"
                  value={story}
                  onChange={(e) => setStory(e.target.value)}
                  className="input"
                  id="storyText"
                />
              </div>
              <div className="buttons">
                <button onClick={tovabb}>Vissza</button>
                <button onClick={modosit}>Ments</button>
                <div className="publishingButton">
                  <p>Publikls</p>
                  <label class="switch">
                    <input
                      type="checkbox"
                      checked={isPublished}
                      onChange={togglePublikalas}
                    />
                    <span class="slider"></span>
                  </label>
                </div>
              </div>
              {error && <div className="error">{error}</div>}
              {success && <div className="success">{success}</div>}
            </div>
          )}
        </div>
      ) : (
        <div>
          <h1 className="errorMessage">
            Ezt a trtnetet nem tudod szerkeszteni!
          </h1>
        </div>
      )}
    </div>
  );
};

export default Updatestory;
