import React, { useState, useEffect } from "react";
import { useAuthContext } from "../../hooks/useAuthContext";
import { Link } from "react-router-dom";
import { useLogout } from "../../hooks/useLogout";
import DamerauLevenshtein from "damerau-levenshtein";
import { ketElem } from "../../components/sorting";
import config from "../../components/config";

const Story = () => {
  const { user } = useAuthContext();
  const { logout } = useLogout();
  const [isLoading, setIsLoading] = useState(false);

  const [felhasznalonev, setFelhasznalonev] = useState("");
  const [osszesStory, SetOsszesStory] = useState([]);

  const [searchTerm, setSearchTerm] = useState("");

  const [magyarChecked, setMagyarChecked] = useState(false);
  const [angolChecked, setAngolChecked] = useState(false);

  const [akcioChecked, setAkcioChecked] = useState(false);
  const [dramaChecked, setDramaChecked] = useState(false);
  const [fantaziaChecked, setFantaziaChecked] = useState(false);
  const [horrorChecked, setHorrorChecked] = useState(false);
  const [humorChecked, setHumorChecked] = useState(false);
  const [kalandChecked, setKalandChecked] = useState(false);
  const [krimiChecked, setKrimiChecked] = useState(false);
  const [paranormalChecked, setParanormalChecked] = useState(false);
  const [romantikaChecked, setRomantikaChecked] = useState(false);
  const [scifiChecked, setScifiChecked] = useState(false);
  const [versChecked, setVersChecked] = useState(false);

  const [star1Src, SetStar1Src] = useState("/images/star.png");
  const [star2Src, SetStar2Src] = useState("/images/star.png");
  const [star3Src, SetStar3Src] = useState("/images/star.png");
  const [star4Src, SetStar4Src] = useState("/images/star.png");
  const [star5Src, SetStar5Src] = useState("/images/star.png");
  const [starBool, setStarBool] = useState(false);
  const [sajatErtekeles, SetSajatErtekeles] = useState(null);

  const [windowSize, setWindowSize] = useState([
    window.innerWidth,
    window.innerHeight,
  ]);
  const [showFilters, SetShowFilters] = useState(false);
  const [filterVisibility, SetFilterVisibility] = useState("visible");

  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(8);

  const url = config.URL;

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(url + "/getInfos", {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${user.token}`,
          },
        });

        if (response.ok) {
          const data = await response.json();
          const isAdmin = data.isAdmin;
          const felhasznalonev = data.felhasznalonev;
          setFelhasznalonev(felhasznalonev);
        }
      } catch (error) {
        console.log("Fetch error:", error);
      }
    };

    fetchData();
  }, [user, felhasznalonev]);

  useEffect(() => {
    setIsLoading(true);
    const fetchData = async () => {
      try {
        const response = await fetch(url + "/story", {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${user.token}`,
          },
        });

        if (response.ok) {
          const data = await response.json();
          SetOsszesStory(data.story);
          setIsLoading(false);
        } else {
          const data = await response.json();
          if (data.msg.includes("Token expired")) {
            logout();
          }
        }
      } catch (error) {
        console.log("Fetch error:", error);
      }
    };

    fetchData();
  }, [user, felhasznalonev]);

  // WINDOWS SIZE DETECTION
  useEffect(() => {
    const handleWindowResize = () => {
      setWindowSize([window.innerWidth, window.innerHeight]);
    };

    window.addEventListener("resize", handleWindowResize);

    return () => {
      window.removeEventListener("resize", handleWindowResize);
    };
  }, []);

  useEffect(() => {
    if (windowSize[0] > 550) {
      SetFilterVisibility("visible");
    } else if (windowSize[0] < 550 && showFilters === false) {
      SetFilterVisibility("hidden");
      setItemsPerPage(4);
    } else if (windowSize[0] < 550 && showFilters === true) {
      SetFilterVisibility("visible");
      setItemsPerPage(4);
    }

    if (windowSize[0] > 1000) {
      setItemsPerPage(8);
    }
  }, [windowSize[0], showFilters]);

  //VARIABLES
  const angol = "angol";
  const magyar = "magyar";

  //SEARCHING
  const levenshteinDistance = (str1, str2) =>
    new DamerauLevenshtein(str1, str2);

  const combinedFilteredStories = osszesStory.filter((story) => {
    // Checkbox filters
    const languageFilter =
      ((!magyarChecked || story.nyelv.toLowerCase() === "magyar") &&
        (!angolChecked || story.nyelv.toLowerCase() === "angol")) ||
      ketElem(angolChecked, magyarChecked, angol, magyar, story.nyelv);

    const categoryFilter =
      (!akcioChecked || story.kategoria.toLowerCase() === "akci") &&
      (!dramaChecked || story.kategoria.toLowerCase() === "drma") &&
      (!fantaziaChecked || story.kategoria.toLowerCase() === "fantzia") &&
      (!horrorChecked || story.kategoria.toLowerCase() === "horror") &&
      (!humorChecked || story.kategoria.toLowerCase() === "humor") &&
      (!kalandChecked || story.kategoria.toLowerCase() === "kaland") &&
      (!krimiChecked || story.kategoria.toLowerCase() === "krimi") &&
      (!paranormalChecked ||
        story.kategoria.toLowerCase() === "paranormlis") &&
      (!romantikaChecked || story.kategoria.toLowerCase() === "romantikus") &&
      (!scifiChecked || story.kategoria.toLowerCase() === "sci-fi") &&
      (!versChecked || story.kategoria.toLowerCase() === "vers");

    // Search filter
    const titleWords = story.cim.toLowerCase().split(" ");
    const author = story.szerzo.toLowerCase();
    const searchTermLower = searchTerm.toLowerCase();

    const hasSimilarTitle = titleWords.some((word) => {
      const distance = levenshteinDistance(word, searchTermLower);
      return distance.similarity >= 0.35;
    });

    const hasSimilarAuthor =
      levenshteinDistance(author, searchTermLower).similarity >= 0.35;

    if (sajatErtekeles !== null) {
      let ertekelesArray = story.ertekelesek.map((item) => item.ertekeles);
      if (ertekelesArray.length > 0) {
        let averageErtekeles =
          ertekelesArray.reduce((acc, value) => acc + value, 0) /
          ertekelesArray.length;
        console.log(averageErtekeles);

        return (
          categoryFilter &&
          languageFilter &&
          (searchTerm.trim() === "" || hasSimilarTitle || hasSimilarAuthor) &&
          (sajatErtekeles === null ||
            sajatErtekeles === undefined ||
            (averageErtekeles >= sajatErtekeles &&
              averageErtekeles < sajatErtekeles + 1))
        );
      } else {
        return (
          categoryFilter &&
          languageFilter &&
          (searchTerm.trim() === "" || hasSimilarTitle || hasSimilarAuthor) &&
          sajatErtekeles === 0
        );
      }
    }
    return (
      categoryFilter &&
      languageFilter &&
      (searchTerm.trim() === "" || hasSimilarTitle || hasSimilarAuthor)
    );
  });

  const sortedStories = combinedFilteredStories.sort((a, b) => {
    const distanceA = levenshteinDistance(
      a.cim.toLowerCase(),
      searchTerm.toLowerCase()
    ).similarity;
    const distanceB = levenshteinDistance(
      b.cim.toLowerCase(),
      searchTerm.toLowerCase()
    ).similarity;

    return distanceB - distanceA;
  });

  const resetAll = () => {
    setSearchTerm("");
    setMagyarChecked(false);
    setAngolChecked(false);
    setAkcioChecked(false);
    setDramaChecked(false);
    setFantaziaChecked(false);
    setHorrorChecked(false);
    setHumorChecked(false);
    setKalandChecked(false);
    setKrimiChecked(false);
    setParanormalChecked(false);
    setRomantikaChecked(false);
    setScifiChecked(false);
    setVersChecked(false);
    SetSajatErtekeles(null);
    setStarBool(false);
    SetStar1Src("/images/star.png");
    SetStar2Src("/images/star.png");
    SetStar3Src("/images/star.png");
    SetStar4Src("/images/star.png");
    SetStar5Src("/images/star.png");
  };

  const resetNyelv = () => {
    setMagyarChecked(false);
    setAngolChecked(false);
  };

  const resetKategoria = () => {
    setAkcioChecked(false);
    setDramaChecked(false);
    setFantaziaChecked(false);
    setHorrorChecked(false);
    setHumorChecked(false);
    setKalandChecked(false);
    setKrimiChecked(false);
    setParanormalChecked(false);
    setRomantikaChecked(false);
    setScifiChecked(false);
    setVersChecked(false);
  };

  const resetErtekeles = () => {
    SetSajatErtekeles(null);
    SetStar1Src("/images/star.png");
    SetStar2Src("/images/star.png");
    SetStar3Src("/images/star.png");
    SetStar4Src("/images/star.png");
    SetStar5Src("/images/star.png");
    setStarBool(false);
  };

  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const paginatedStories = sortedStories.slice(startIndex, endIndex);

  return (
    <div className="stories">
      {windowSize[0] < 550 && (
        <span
          className="material-symbols-outlined"
          style={{ fontSize: "40px", color: "#363061", cursor: "pointer" }}
          onClick={() => {
            SetShowFilters(!showFilters);
          }}
        >
          {showFilters ? "close" : "filter_alt"}
        </span>
      )}

      <div className="storyWrap">
        {!isLoading ? <div></div> : <div className="loader"></div>}
        <div
          style={{ visibility: `${filterVisibility}` }}
          className={
            windowSize[0] > 550
              ? "sortingContainer"
              : "sortingContainerResponsive"
          }
        >
          <p className="default" onClick={resetAll}>
            Alaprtelmezett
          </p>
          <input
            className="input"
            type="text"
            placeholder="Keress..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          <div className="nyelv">
            <div className="filterClose">
              <span>Nyelv</span>
              {magyarChecked || angolChecked ? (
                <span
                  className="material-symbols-outlined"
                  onClick={resetNyelv}
                >
                  close
                </span>
              ) : (
                ""
              )}
            </div>

            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="ch1"
                    type="checkbox"
                    checked={magyarChecked}
                    onChange={(e) => setMagyarChecked(e.target.checked)}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Magyar</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="ch1"
                    type="checkbox"
                    checked={angolChecked}
                    onChange={(e) => setAngolChecked(e.target.checked)}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Angol</p>
            </div>
          </div>
          <div className="kategoria" id="kategoria">
            <div className="filterClose">
              <span>Kategria</span>
              {akcioChecked ||
              dramaChecked ||
              fantaziaChecked ||
              horrorChecked ||
              humorChecked ||
              kalandChecked ||
              krimiChecked ||
              paranormalChecked ||
              romantikaChecked ||
              scifiChecked ||
              versChecked ? (
                <span
                  className="material-symbols-outlined"
                  onClick={resetKategoria}
                >
                  close
                </span>
              ) : (
                ""
              )}
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="akcio"
                    type="radio"
                    name="kateg"
                    checked={akcioChecked}
                    onChange={(e) => {
                      setAkcioChecked(e.target.checked);
                      setDramaChecked(false);
                      setFantaziaChecked(false);
                      setHorrorChecked(false);
                      setHumorChecked(false);
                      setKalandChecked(false);
                      setKrimiChecked(false);
                      setParanormalChecked(false);
                      setRomantikaChecked(false);
                      setScifiChecked(false);
                      setVersChecked(false);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Akci</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="drama"
                    type="radio"
                    name="kateg"
                    checked={dramaChecked}
                    onChange={(e) => {
                      setAkcioChecked(false);
                      setDramaChecked(e.target.checked);
                      setFantaziaChecked(false);
                      setHorrorChecked(false);
                      setHumorChecked(false);
                      setKalandChecked(false);
                      setKrimiChecked(false);
                      setParanormalChecked(false);
                      setRomantikaChecked(false);
                      setScifiChecked(false);
                      setVersChecked(false);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Drma</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="fantazia"
                    type="radio"
                    name="kateg"
                    checked={fantaziaChecked}
                    onChange={(e) => {
                      setAkcioChecked(false);
                      setDramaChecked(false);
                      setFantaziaChecked(e.target.checked);
                      setHorrorChecked(false);
                      setHumorChecked(false);
                      setKalandChecked(false);
                      setKrimiChecked(false);
                      setParanormalChecked(false);
                      setRomantikaChecked(false);
                      setScifiChecked(false);
                      setVersChecked(false);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Fantzia</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="horror"
                    type="radio"
                    name="kateg"
                    checked={horrorChecked}
                    onChange={(e) => {
                      setAkcioChecked(false);
                      setDramaChecked(false);
                      setFantaziaChecked(false);
                      setHorrorChecked(e.target.checked);
                      setHumorChecked(false);
                      setKalandChecked(false);
                      setKrimiChecked(false);
                      setParanormalChecked(false);
                      setRomantikaChecked(false);
                      setScifiChecked(false);
                      setVersChecked(false);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Horror</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="humor"
                    type="radio"
                    name="kateg"
                    checked={humorChecked}
                    onChange={(e) => {
                      setAkcioChecked(false);
                      setDramaChecked(false);
                      setFantaziaChecked(false);
                      setHorrorChecked(false);
                      setHumorChecked(e.target.checked);
                      setKalandChecked(false);
                      setKrimiChecked(false);
                      setParanormalChecked(false);
                      setRomantikaChecked(false);
                      setScifiChecked(false);
                      setVersChecked(false);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Humor</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="kaland"
                    type="radio"
                    name="kateg"
                    checked={kalandChecked}
                    onChange={(e) => {
                      setAkcioChecked(false);
                      setDramaChecked(false);
                      setFantaziaChecked(false);
                      setHorrorChecked(false);
                      setHumorChecked(false);
                      setKalandChecked(e.target.checked);
                      setKrimiChecked(false);
                      setParanormalChecked(false);
                      setRomantikaChecked(false);
                      setScifiChecked(false);
                      setVersChecked(false);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Kaland</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="krimi"
                    type="radio"
                    name="kateg"
                    checked={krimiChecked}
                    onChange={(e) => {
                      setAkcioChecked(false);
                      setDramaChecked(false);
                      setFantaziaChecked(false);
                      setHorrorChecked(false);
                      setHumorChecked(false);
                      setKalandChecked(false);
                      setKrimiChecked(e.target.checked);
                      setParanormalChecked(false);
                      setRomantikaChecked(false);
                      setScifiChecked(false);
                      setVersChecked(false);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Krimi</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="paranormal"
                    type="radio"
                    name="kateg"
                    checked={paranormalChecked}
                    onChange={(e) => {
                      setAkcioChecked(false);
                      setDramaChecked(false);
                      setFantaziaChecked(false);
                      setHorrorChecked(false);
                      setHumorChecked(false);
                      setKalandChecked(false);
                      setKrimiChecked(false);
                      setParanormalChecked(e.target.checked);
                      setRomantikaChecked(false);
                      setScifiChecked(false);
                      setVersChecked(false);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Paranormlis</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="romantika"
                    type="radio"
                    name="kateg"
                    checked={romantikaChecked}
                    onChange={(e) => {
                      setAkcioChecked(false);
                      setDramaChecked(false);
                      setFantaziaChecked(false);
                      setHorrorChecked(false);
                      setHumorChecked(false);
                      setKalandChecked(false);
                      setKrimiChecked(false);
                      setParanormalChecked(false);
                      setRomantikaChecked(e.target.checked);
                      setScifiChecked(false);
                      setVersChecked(false);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Romantikus</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="scifi"
                    type="radio"
                    name="kateg"
                    checked={scifiChecked}
                    onChange={(e) => {
                      setAkcioChecked(false);
                      setDramaChecked(false);
                      setFantaziaChecked(false);
                      setHorrorChecked(false);
                      setHumorChecked(false);
                      setKalandChecked(false);
                      setKrimiChecked(false);
                      setParanormalChecked(false);
                      setRomantikaChecked(false);
                      setScifiChecked(e.target.checked);
                      setVersChecked(false);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Sci-Fi</p>
            </div>
            <div className="checkbox">
              <div class="content">
                <label class="checkBox">
                  <input
                    id="vers"
                    type="radio"
                    name="kateg"
                    checked={versChecked}
                    onChange={(e) => {
                      setAkcioChecked(false);
                      setDramaChecked(false);
                      setFantaziaChecked(false);
                      setHorrorChecked(false);
                      setHumorChecked(false);
                      setKalandChecked(false);
                      setKrimiChecked(false);
                      setParanormalChecked(false);
                      setRomantikaChecked(false);
                      setScifiChecked(false);
                      setVersChecked(e.target.checked);
                    }}
                  />
                  <div class="transition"></div>
                </label>
              </div>
              <p>Vers</p>
            </div>
          </div>
          <div className="ertekeles">
            <div className="filterClose">
              <span>rtkels</span>
              {starBool ? (
                <span
                  className="material-symbols-outlined"
                  onClick={resetErtekeles}
                >
                  close
                </span>
              ) : (
                ""
              )}
            </div>
            <div className="sortRating">
              <img
                onClick={() => {
                  SetStar1Src("/images/starfilled.png");
                  SetStar2Src("/images/star.png");
                  SetStar3Src("/images/star.png");
                  SetStar4Src("/images/star.png");
                  SetStar5Src("/images/star.png");
                  setStarBool(true);
                  SetSajatErtekeles(1);
                }}
                src={star1Src}
                alt=""
              />
              <img
                onClick={() => {
                  SetStar1Src("/images/starfilled.png");
                  SetStar2Src("/images/starfilled.png");
                  SetStar3Src("/images/star.png");
                  SetStar4Src("/images/star.png");
                  SetStar5Src("/images/star.png");
                  setStarBool(true);
                  SetSajatErtekeles(2);
                }}
                src={star2Src}
                alt=""
              />
              <img
                onClick={() => {
                  SetStar1Src("/images/starfilled.png");
                  SetStar2Src("/images/starfilled.png");
                  SetStar3Src("/images/starfilled.png");
                  SetStar4Src("/images/star.png");
                  SetStar5Src("/images/star.png");
                  setStarBool(true);
                  SetSajatErtekeles(3);
                }}
                src={star3Src}
                alt=""
              />
              <img
                onClick={() => {
                  SetStar1Src("/images/starfilled.png");
                  SetStar2Src("/images/starfilled.png");
                  SetStar3Src("/images/starfilled.png");
                  SetStar4Src("/images/starfilled.png");
                  SetStar5Src("/images/star.png");
                  setStarBool(true);
                  SetSajatErtekeles(4);
                }}
                src={star4Src}
                alt=""
              />
              <img
                onClick={() => {
                  SetStar1Src("/images/starfilled.png");
                  SetStar2Src("/images/starfilled.png");
                  SetStar3Src("/images/starfilled.png");
                  SetStar4Src("/images/starfilled.png");
                  SetStar5Src("/images/starfilled.png");
                  setStarBool(true);
                  SetSajatErtekeles(5);
                }}
                src={star5Src}
                alt=""
              />
            </div>
            <div className="ertekelesLink">
              <Link
                onClick={() => {
                  SetStar1Src("/images/star.png");
                  SetStar2Src("/images/star.png");
                  SetStar3Src("/images/star.png");
                  SetStar4Src("/images/star.png");
                  SetStar5Src("/images/star.png");
                  setStarBool(true);
                  SetSajatErtekeles(0);
                }}
              >
                Nem rtkelt
              </Link>
            </div>
          </div>
        </div>
        <div className="paginationAndStories">
          <div className="storyContainer" id="allStoryContainer">
            {paginatedStories.map((story) => (
              <div className="storyLink" id="profileStories" key={story._id}>
                <Link to={`/story/${story._id}`}>
                  <div className="book-container" key={story._id}>
                    <div className="book">
                      <div className="front-content">
                        <img src={story.boritokep} alt="" />
                      </div>
                      <div className="content">
                        <p className="heading">{story.cim}</p>
                        <p>{story.leiras}</p>
                        <div id="author">
                          <h3>{story.szerzo}</h3>
                        </div>
                      </div>
                    </div>
                  </div>
                </Link>
              </div>
            ))}
          </div>
          <div className="pagination">
            <span id="currentPage">{`${currentPage}. oldal`}</span>
            <div className="paginationButtons">
              <button
                onClick={() =>
                  setCurrentPage((prevPage) => Math.max(prevPage - 1, 1))
                }
                disabled={currentPage === 1}
              >
                <span class="material-symbols-outlined">chevron_left</span>
              </button>
              <button
                onClick={() =>
                  setCurrentPage((prevPage) =>
                    Math.min(
                      prevPage + 1,
                      Math.ceil(sortedStories.length / itemsPerPage)
                    )
                  )
                }
                disabled={
                  currentPage === Math.ceil(sortedStories.length / itemsPerPage)
                }
              >
                <span class="material-symbols-outlined">chevron_right</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Story;
